/* Color para los bordes de la primera tarjeta*/
.box1{
border-top: 8px solid #d11507;
border-top: 8px solid var(--color1);
border-bottom: 8px solid #d11507;
border-bottom: 8px solid var(--color1);
border-left: 8px solid #d11507;
border-left: 8px solid var(--color1);
border-right: 8px solid #d11507;
border-right: 8px solid var(--color1);
transform: translateX(-28em);
animation: primero .5s linear forwards;
cursor: pointer;
}
/* Color para los bordes de la segunda tarjeta*/
.box2{
border-top: 8px solid #39b3e9;
border-top: 8px solid var(--color2);
