border-bottom: 8px solid var(--color2);
border-left: 8px solid #39b3e9;
border-left: 8px solid var(--color2);
border-right: 8px solid #39b3e9;
border-right: 8px solid var(--color2);
transform: translateX(-28em);
animation: primero .5s linear forwards;
cursor: pointer;
}
