
  #mainPrincipal {
    background-image: url(../img/fondo.png);
    background-position: center center;
    background-size: cover;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 20px;
 
  }
  
  .viewSession {
    right: 0;
  }
  .header {
    width: 100%;
    background-image: red;
    padding-top: 90px ;
    padding-right: 43%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  .header .links {
    display: flex;
  }
  .header .links .link {
      text-decoration: none;
    /* position: relative; */
    color: white;
    background-color: #0079C1;
    border: 1px solid #0079C1;
    border-radius: 5px;
    padding: 15px 35px;
    margin-top: 180% ;
    margin-left: -2%;
    cursor: pointer;
  }
  .header .links .link:hover {
    color: #0079C1;
    background-color: white;
  }
  .header .logo {
    color: white;
    font-size: 3em;
  }
