.separator {
    display: flex;
    align-items: center;
    text-align: center;
	font-family: "AvenirNextLTPro-Bold", Sans-serif;
  }
  .separator::before, .separator::after {
    content: '';
    flex: 1;
    border-bottom: 1px solid #DEDEDE;
  }
  .separator::before {
    margin-right: .25em;
  }
  .separator::after {
    margin-left: .25em;
  }
    
    /* terjetas */
    * {
		box-sizing: border-box;
   }
	.blog-card {
		display: flex;
		flex-direction: column;
		margin: 1rem auto;
		box-shadow: 0 3px 7px -1px rgba(0, 0, 0, .1);
		margin-bottom: 1.6%;
		/* background: -webkit-gradient(linear, left top, right top, from(#83B1CB), to(#0044B1)); */
        background: linear-gradient(to right, #0044B1,#83B1CB);
		line-height: 1.4;
		font-family: "AvenirNextLTPro-Light", Sans-serif;
        border-radius: 5px;
		overflow: hidden;
		z-index: 0;
   }
	.blog-card a {
		color: inherit;
   }
	.blog-card a:hover {
		color: #5ad67d;
   }
	.blog-card:hover .photo {
		transform: scale(1.3) rotate(3deg);
   }
	.blog-card .meta {
		position: relative;
		z-index: 0;
		height: 200px;
   }
	.blog-card .photo {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-size: cover;
		background-position: center;
		transition: transform 0.2s;
   }
	.blog-card .details, .blog-card .details ul {
		margin: auto;
		padding: 0;
		list-style: none;
   }
	.blog-card .details {
		position: absolute;
		top: 0;
		bottom: 0;
		left: -100%;
		margin: auto;
		transition: left 0.2s;
		background: rgba(0, 0, 0, .6);
		color: #fff;
		padding: 10px;
		width: 100%;
		font-size: 0.9rem;
   }
	.blog-card .details a {
		text-decoration: dotted underline;
   }
	.blog-card .details ul li {
		display: inline-block;
   }
	.blog-card .details .author:before {
		font-family: FontAwesome;
		margin-right: 10px;
		content: "\f007";
   }
	.blog-card .details .tags ul:before {
		font-family: FontAwesome;
		content: "\f02b";
		margin-right: 10px;
   }
	.blog-card .details .tags li {
		margin-right: 2px;
   }
	.blog-card .details .tags li:first-child {
		margin-left: -4px;
   }
   /* cambiar color tergetas  */
	.blog-card .description {
		padding: 1rem;
		background: -webkit-gradient(linear, left top, right top, from(#83B1CB), to(#0044B1));
    background: linear-gradient(to right, #0044B1,#83B1CB);
		position: relative;
		z-index: 1;
   }
