}
	.blog-card .description h1, .blog-card .description h2 {
		font-family: "AvenirNextLTPro-Bold", Sans-serif;
   }
	.blog-card .description h1 {
        line-height: 1;
        color: white;
		margin: 0;
		font-size: 1.7rem;
   }
	.blog-card .description h2 {
		font-size: 1rem;
		font-weight: 300;
		text-transform: uppercase;
		color: #a2a2a2;
		margin-top: 5px;
   }
	.blog-card p {
		position: relative;
		margin: 1rem 0 0;
   }
	.blog-card p:first-of-type {
		margin-top: 1.25rem;
   }
	.blog-card p:first-of-type:before {
		content: "";
		position: absolute;
		height: 5px;
		background: #5ad67d;
		width: 35px;
		top: -0.75rem;
		border-radius: 3px;
   }
	.blog-card:hover .details {
		left: 0%;
   }
	@media (min-width: 640px) {
		.blog-card {
			flex-direction: row;
			max-width: 700px;
	   }
		.blog-card .meta {
            
			flex-basis: 40%;
            height: 200px;
            opacity: 0.9;
	   }
		.blog-card .description {
			flex-basis: 60%;
	   }
		.blog-card .description:before {
			transform: skewX(-3deg);
			content: "";
			background: #fff;
			width: 20px;
			position: absolute;
			left: -10px;
			top: 0;
			bottom: 0;
			z-index: -1;
	   }
		.blog-card.alt {
			flex-direction: row-reverse;
	   }
		.blog-card.alt .description:before {
			left: inherit;
			right: -10px;
			transform: skew(3deg);
	   }
		.blog-card.alt .details {
			padding-left: 25px;
	   }
   }
